/* ------------------------------------------------------------------ */
/* 1. General Styles
/* ------------------------------------------------------------------ */

body {
  background: #0f0f0f;
  overflow-y: auto;
}

.section-title {
  border: 2px solid;
  font-family: "Open Sans", sans-serif;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 3px;
  line-height: 36px;
  margin: 0 auto;
  max-width: 420px;
  min-width: 250px;
  padding: 20px;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
}

.section-title:after {
  background: none repeat scroll 0 0 #fff;
  bottom: -10px;
  color: #333;
  content: "\f0a7";
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  line-height: 1px;
  padding: 5px;
  position: absolute;
  font-family: "FontAwesome";
}

/* ------------------------------------------------------------------ */
/* 2. Preloader CSS */
/* ------------------------------------------------------------------ */

#preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: #57bddb;
  background: -webkit-linear-gradient(
    90deg,
    #4cb8c4 10%,
    #3cd3ad 90%
  ); /* Chrome 10+, Saf5.1+ */ /* FF3.6+ */ /* IE10 */
  background: -o-linear-gradient(
    90deg,
    #4cb8c4 10%,
    #3cd3ad 90%
  ); /* Opera 11.10+ */
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, #4cb8c4), color-stop(90%, #3cd3ad));
  background: -webkit-linear-gradient(left, #4cb8c4 10%, #3cd3ad 90%);
  background: -o-linear-gradient(left, #4cb8c4 10%, #3cd3ad 90%);
  background: linear-gradient(90deg, #4cb8c4 10%, #3cd3ad 90%); /* W3C */
}

.loader {
  -webkit-animation: animate 1.5s linear infinite;
          animation: animate 1.5s linear infinite;
  height: 80px;
  width: 80px;
  position: absolute;
  left: calc(50% - 40px);
  top: calc(50% - 40px);
  z-index: 999;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(220deg);
            transform: rotate(220deg);
  }
}

@keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(220deg);
            transform: rotate(220deg);
  }
}
.loader:after {
  -webkit-animation: animate2 1.5s ease-in-out infinite;
          animation: animate2 1.5s ease-in-out infinite;
  content: "";
  border-radius: 50%;
  height: 80px;
  width: 80px;
  position: absolute;
}
@-webkit-keyframes animate2 {
  0% {
    -webkit-box-shadow: inset #fff 0 0 0 17px;
            box-shadow: inset #fff 0 0 0 17px;
    -webkit-transform: rotate(-140deg);
            transform: rotate(-140deg);
  }
  50% {
    -webkit-box-shadow: inset #fff 0 0 0 2px;
            box-shadow: inset #fff 0 0 0 2px;
  }
  100% {
    -webkit-box-shadow: inset #fff 0 0 0 17px;
            box-shadow: inset #fff 0 0 0 17px;
    -webkit-transform: rotate(140deg);
            transform: rotate(140deg);
  }
}
@keyframes animate2 {
  0% {
    -webkit-box-shadow: inset #fff 0 0 0 17px;
            box-shadow: inset #fff 0 0 0 17px;
    -webkit-transform: rotate(-140deg);
            transform: rotate(-140deg);
  }
  50% {
    -webkit-box-shadow: inset #fff 0 0 0 2px;
            box-shadow: inset #fff 0 0 0 2px;
  }
  100% {
    -webkit-box-shadow: inset #fff 0 0 0 17px;
            box-shadow: inset #fff 0 0 0 17px;
    -webkit-transform: rotate(140deg);
            transform: rotate(140deg);
  }
}

/* ------------------------------------------------------------------ */
/* 3. Header Styles
/* ------------------------------------------------------------------ */

header {
  position: relative;
  height: 800px;
  min-height: 500px;
  width: 100%;
  background: #161415 url(../images/bg-blue.png) no-repeat center center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
}

/* vertically center banner section */
header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
header .banner {
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
  z-index: 1;
  position: relative;
}

header .banner-text {
  width: 100%;
}
header .banner-text h1 {
  font-family: "Raleway", sans-serif;
  color: #fff;
  letter-spacing: 2px;
  margin: 0 auto 40px;
}

header .banner-text h1 span {
  font-weight: 900;
  text-transform: uppercase;
}

header .banner-text h3 {
  font-family: "Raleway", sans-serif;
  color: #fefefe;
  margin: 0 auto;
  width: 70%;
}
header .banner-text h3 span,
header .banner-text h3 a {
  color: #fff;
  font-weight: bold;
}
header .banner-text hr {
  width: 60%;
  margin: 18px auto 24px auto;
  border-color: #2f2d2e;
  border-color: rgba(150, 150, 150, 0.1);
}

.header-overlay {
  background: #161415 url(../images/bg-blue-blur.png) no-repeat center center;
  background-size: cover !important;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0;
}

/* social links */

#social {
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: block;
  z-index: 2;
}

.social-container {
  display: block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  text-align: center;
}

.social-container .social {
  margin: 24px 0;
  padding: 0;
  font-size: 30px;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}
.social-container .social li {
  display: inline-block;
  margin: 0 15px;
  padding: 0;
}
.social-container .social li a {
  color: #fff;
}
.social-container .social li a:hover {
  color: #11abb0;
}

/* scrolldown link */
header .scrolldown a {
  position: absolute;
  bottom: 130px;
  left: 50%;
  margin-left: -29px;
  color: #fff;
  display: block;
  height: 42px;
  width: 42px;
  font-size: 42px;
  line-height: 42px;
  z-index: 3;
  border-radius: 100%;
  -webkit-animation: updown 0.5s linear 1s infinite alternate; /* Chrome, Safari, Opera */
  animation: updown 0.5s linear 1s infinite alternate;

  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes updown {
  from {
    -webkit-transform: scale(1);
    bottom: 130px;
  }
  to {
    -webkit-transform: scale(1.2);
    bottom: 125px;
  }
}

/* Standard syntax */
@keyframes updown {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    bottom: 130px;
  }
  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    bottom: 125px;
  }
}

header .scrolldown a:hover {
  color: #11abb0;
}

/* primary navigation
--------------------------------------------------------------------- */
