/* Timeline Layout */

/* Styling */
.timeline {
  margin: 15px auto;
  position: relative;
  max-width: 100%;
}
.timeline:before {
  background: #11abb0;
  content: "";
  margin-left: -1px;
  position: absolute;
  top: 0;
  left: 32px;
  width: 2px;
  height: 100%;
}

.timeline-event {
  position: relative;
}
.timeline-event:hover .timeline-event-icon {
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background-color: #ed5565;
}

.timeline-event:hover .timeline-event-thumbnail {
  -webkit-box-shadow: inset 40em 0 0 0 #ed5565;
  box-shadow: inset 40em 0 0 0 #ed5565;
}

.timeline-event h3 {
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
}

.timeline-event:hover h3 {
  color: #ed5565;
}

.timeline-event-copy {
  left: 50px;
  padding: 32px 32px 0;
  position: relative;
  top: -32px;
  width: 80%;
}

.timeline-event-copy h3 {
  font-size: 28px;
}
.timeline-event-copy h4 {
  font-size: 20px;
  margin-bottom: 15px;
  font-family: "Raleway", sans-serif;
}
.timeline-event-copy strong {
  font-weight: 700;
}
.timeline-event-copy p:not(.timeline-event-thumbnail) {
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  padding-bottom: 20px;
}

.timeline-event-icon {
  -o-transition: -o-transform 0.2s ease-in;
  -webkit-transition: -webkit-transform 0.2s ease-in;
  transition: -webkit-transform 0.2s ease-in;
  -o-transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background-color: black;
  outline: 10px solid white;
  display: block;
  margin: 8px 8px 8px -8px;
  position: absolute;
  top: 0;
  left: 32px;
  width: 16px;
  height: 16px;
}

.timeline-event-thumbnail {
  -moz-transition: box-shadow 0.5s ease-in 0.1s;
  -o-transition: box-shadow 0.5s ease-in 0.1s;
  -webkit-transition: box-shadow 0.5s ease-in;
  -webkit-transition-delay: 0.1s;
  -webkit-transition: -webkit-box-shadow 0.5s ease-in 0.1s;
  transition: -webkit-box-shadow 0.5s ease-in 0.1s;
  transition: box-shadow 0.5s ease-in 0.1s;
  transition: box-shadow 0.5s ease-in 0.1s, -webkit-box-shadow 0.5s ease-in 0.1s;
  -webkit-box-shadow: inset 0 0 0 0em #11abb0;
  box-shadow: inset 0 0 0 0em #11abb0;
  background: none repeat scroll 0 0 #ed5565;
  -webkit-box-shadow: 0 0 0 0 #11abb0 inset;
          box-shadow: 0 0 0 0 #11abb0 inset;
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
  padding: 8px 15px;
  transition: box-shadow 0.5s ease-in 0.1s;
}

