  overflow: hidden;
  position: relative;

  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.portfolio-item .item-wrap a {
  display: block;
  cursor: pointer;
}

/* overlay */
.portfolio-item .item-wrap .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);

  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;

  background: #ed5565;
  background: rgba(237, 85, 101, 0.9);
}
.portfolio-item .item-wrap .link-icon {
  display: block;
  color: #fff;
  height: 30px;
  width: 30px;
  font-size: 18px;
  line-height: 30px;
  text-align: center;

  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);

  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -15px;
}
.portfolio-item .item-wrap img {
  vertical-align: bottom;
}
.portfolio-item .portfolio-item-meta {
  padding: 18px;
}
.portfolio-item .portfolio-item-meta h5 {
  font: 14px/21px "Open Sans", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
}
.portfolio-item .portfolio-item-meta p {
  font: 12px/18px "Open Sans", sans-serif;
  font-weight: 300;
  color: #efefef;
  margin-bottom: 0;
  text-align: center;
}

/* on hover */
.portfolio-item:hover .overlay {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}
.portfolio-item:hover .link-icon {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}

/* popup modal */
.popup-modal {
  max-width: 550px;
  background: #fff;
  position: relative;
  margin: 0 auto;
}
.popup-modal .description-box {
  padding: 12px 36px 18px 36px;
}
.popup-modal .description-box h4 {
  font: 15px/24px "Open Sans", sans-serif;
  font-weight: 700;
  margin-bottom: 12px;
  color: #111;
}
.popup-modal .description-box p {
  font: 14px/24px "Open Sans", sans-serif;
  color: #a1a1a1;
  margin-bottom: 12px;
}
.popup-modal .description-box .categories {
  font: 11px/21px "Open Sans", sans-serif;
  font-weight: 300;
  color: #a1a1a1;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: block;
  text-align: left;
}
.popup-modal .description-box .categories i {
  margin-right: 8px;
}
.popup-modal .link-box {
  padding: 18px 36px;
  background: #111;
  text-align: left;
}
.popup-modal .link-box a {
  color: #fff;
  font: 11px/21px "Open Sans", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 3px;
  cursor: pointer;
}
.popup-modal a:hover {
  color: #00cccc;
}
.popup-modal a.popup-modal-dismiss {
  margin-left: 24px;
}

/* fadein/fadeout effect for modal popup
/* ------------------------------------------------------------------ */

/* content at start */
.mfp-fade.mfp-wrap .mfp-content .popup-modal {
  opacity: 0;
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}
/* content fadein */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content .popup-modal {
  opacity: 1;
}
/* content fadeout */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content .popup-modal {
  opacity: 0;
}

/* ------------------------------------------------------------------ */
/* 7. Call To Action Section
/* ------------------------------------------------------------------ */

#call-to-action {
  background: #212121;
  padding-top: 66px;
  padding-bottom: 48px;
}
#call-to-action h1 {
  font: 18px/24px "opensans-bold", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #fff;
  text-align: center;
}
#call-to-action h1 span {
  display: block;
}

#call-to-action .action {
  margin-top: 12px;
}
#call-to-action h2 {
  font: 28px/36px "Open Sans", sans-serif;
  font-weight: 700;
  color: #ebeeee;
  margin-bottom: 6px;
  text-transform: uppercase;
}
#call-to-action h2 a {
  color: inherit;
}
#call-to-action p {
  color: #888;
  font-size: 17px;
}

#call-to-action .button:hover,
#call-to-action .button:active {
  background: #ffffff;
  color: #0d0d0d;
}
#call-to-action p span {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  color: #d8d8d8;
}

/* ------------------------------------------------------------------
/* 8. Testimonials
/* ------------------------------------------------------------------ */

#testimonials {
  background: #f1f1f1;
  background: white;
  position: relative;
  min-height: 200px;
  width: 100%;
  overflow: hidden;
}
#testimonials .text-container {
  padding-top: 10px;
  padding-bottom: 66px;
}
#testimonials h1 {
  color: #95a3a3;
  margin: 50px auto;
  width: 420px;
  position: relative;
}
#testimonials h1:hover {
  border-color: #111;
}
#testimonials h1 span {
  display: block;
}

#testimonials .section-title:after {
  background: #f1f1f1;
  background: white;
  content: "\f0e6";
  color: #95a3a3;
}

#testimonials .image-container {
  text-align: center;
}

#testimonials .image-container img {
  max-width: 100%;
  width: auto;
  margin: 0 auto;
}

#testimonials .header-col {
  padding-top: 9px;
}

/*	Blockquotes */
#testimonials blockquote {
  margin: 0 0px 30px 0px;
  padding-left: 0;
  position: relative;
}
#testimonials blockquote:before {
  content: none;
}
#testimonials blockquote p {
  font-family: "Raleway", sans-serif;
  font-style: italic;
  text-align: center;
  padding: 0;
  font-size: 16px;
  line-height: 28px;
  color: #95a3a3;
}
#testimonials blockquote cite {
  display: block;
  font-size: 12px;
  text-align: center;
  font-style: normal;
  line-height: 18px;
  color: #95a3a3;
}
#testimonials blockquote cite:before {
  content: "\2014 \0020";
}
#testimonials blockquote cite a,
#testimonials blockquote cite a:visited {
  color: #8b9798;
  border: none;
}

/* ------------------------------------------------------------------ */
/* 9. Contact Section
/* ------------------------------------------------------------------ */

#contact {
  background: #191919;
  padding-top: 60px;
  color: #888;
}
#contact .section-head {
  margin-bottom: 30px;
}

#contact a,
#contact a:visited {
  color: #11abb0;
}
#contact a:hover,
#contact a:focus {
  color: #fff;
}

#contact h1 {
  color: #ebeeee;
  border-color: #666;
  margin-bottom: 50px;
}
#contact h1:hover {
  border-color: #fff;
}

#contact .section-title:after {
  background: #191919;
  content: "\f003";
  color: #888;
}

#contact h4 {
  font: 16px/24px "Open Sans", sans-serif;
  font-weight: 700;
  color: #ebeeee;
  margin-bottom: 6px;
}
#contact p.lead {
  font: 18px/36px "Open Sans", sans-serif;
  font-weight: 300;
  padding-right: 3%;
  text-align: center;
  padding: 10px 10%;
}
#contact .header-col {
  padding-top: 6px;
}

/* contact form */

.form-container {
  width: 90%;
  margin: 0 auto;
}
#contact form {
  margin-bottom: 30px;
}
#contact label {
  font: 15px/24px "Open Sans", sans-serif;
  font-weight: 700;
  margin: 12px 0;
  color: #ebeeee;
  display: inline-block;
  float: left;
  width: 10%;
}
#contact input,
#contact textarea,
#contact select {
  font-family: "Open Sans", sans-serif;
  padding: 10px 20px;
  color: #eee;
  background: #373233;
  margin: 0 auto 30px;
  border: 1px solid #373233;
  outline: none;
  font-size: 15px;
  line-height: 24px;
  width: 90%;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
#contact input:focus,
#contact textarea:focus,
#contact select:focus {
  color: #fff;
  border: 1px solid #11abb0;
}

#contact button.submit {
  background: none repeat scroll 0 0 #0d0d0d;
  border: medium none;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  height: auto;
  letter-spacing: 3px;
  margin: 0 auto 0 5%;
  text-transform: uppercase;
}

#contact button.submit:hover {
  color: #0d0d0d;
  background: #fff;
}
#contact span.required {
  color: #11abb0;
  font-size: 13px;
}
#message-warning {
  background: none repeat scroll 0 0 #0f0f0f;
  display: none;
  font-size: 13px;
  margin: 0 auto 20px;
  text-align: center;
  width: 100%;
}

#message-success {
  background: none repeat scroll 0 0 #0f0f0f;
  display: none;
  font-size: 16px;
  margin: 0 auto 20px;
  padding: 50px 15px;
  text-align: center;
  width: 90%;
}
#message-warning {
  color: #d72828;
}
#message-success {
  color: #11abb0;
}

#message-warning i,
#message-success i {
  margin-right: 10px;
}
#image-loader {
  display: none;
  position: relative;
  left: 18px;
  top: 17px;
}

/* Twitter Feed */
#twitter {
  margin-top: 12px;
  padding: 0;
}
#twitter li {
  margin: 6px 0px 12px 0;
  line-height: 30px;
}
#twitter li span {
  display: block;
}
#twitter li b a {
  font: 13px/36px "Open Sans", sans-serif;
  font-weight: 700;
  color: #474747 !important;
  border: none;
}

/* google map */
.map-container {
  margin-top: 30px;
}

#map {
  background: url(../images/map-marker.png) no-repeat center center;
  display: block;
  width: 100%;
  height: 450px;
  margin: 0 auto;
  -webkit-box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.2);
}

#map img {
  max-width: inherit !important;
}

/* ------------------------------------------------------------------ */
/* 10. Footer
/* ------------------------------------------------------------------ */

footer {
  padding-top: 48px;
  margin-bottom: 48px;
  color: #303030;
  font-size: 14px;
  text-align: center;
  position: relative;
}

footer a,
footer a:visited {
  color: #525252;
}
footer a:hover,
footer a:focus {
  color: #fff;
}

/* copyright */
footer .copyright {
  margin: 0;
  padding: 0;
}
footer .copyright li {
  display: inline-block;
  margin: 0;
  padding: 0;
  line-height: 24px;
}
.ie footer .copyright li {
  display: inline;
}
footer .copyright li:before {
  content: "\2022";
  padding-left: 10px;
  padding-right: 10px;
  color: #095153;
}
footer .copyright li:first-child:before {
  display: none;
}

/* social links */
footer .social-links {
  margin: 18px 0 30px 0;
  padding: 0;
  font-size: 30px;
}
footer .social-links li {
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-left: 42px;
  color: #f06000;
}

footer .social-links li:first-child {
  margin-left: 0;
}

/* Go To Top Button */
#go-top {
  position: absolute;
  top: -24px;
  left: 50%;
  margin-left: -30px;
}
#go-top a {
  text-decoration: none;
  border: 0 none;
  display: block;
  width: 60px;
  height: 60px;
  background-color: #525252;
  -webkit-box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;

  color: #fff;
  font-size: 21px;
  line-height: 60px;
  border-radius: 100%;
}

#go-top a:hover {
  background-color: #0f9095;
}



